.customer {
  padding: 60px 0 80px;
}
.customer section {
  max-width: 800px;
  padding: 40px;
  background-color: #f3f3f3;
}
.customer form > div {
  margin-bottom: 24px;
}
.customer .description {
  margin-bottom: 36px;
  padding-bottom: 36px;
  color: #888;
  font-size: 16px;
  font-weight: bold;
  border-bottom: 1px solid #e5e5e5;
}
.customer .input_box {
  display: flex;
  flex-direction: column;
}
.customer .input_box input,
select {
  height: 42px;
  padding: 0 12px;
  font-size: 18px;
}
.customer .input_box input,
select,
textarea {
  border: 1px solid #e5e5e5;
}
.customer .input_box label {
  margin-bottom: 6px;
}
.customer select {
  position: relative;
  appearance: none;
  background: #fff url(/public/images/select_arrow_ico.png) no-repeat 97% 50%;
}
.customer label:not([for="agree"]) {
  font-size: 18px;
  font-weight: bold;
}
.customer textarea {
  padding: 20px;
  resize: none;
}
.customer .agree_box {
  margin-top: 38px;
}
.customer .agree_box > div:not(.checkbox_box) {
  margin-top: 18px;
  padding: 30px;
  background-color: #fff;
}
.customer .agree_box dl:not(:last-of-type) {
  margin-bottom: 24px;
}
.customer .agree_box dt {
  font-size: 16px;
  font-weight: bold;
}
.customer .agree_box dd {
  font-size: 16px;
  color: #888;
}
.customer .checkbox_box {
  margin-top: 18px;
  display: flex;
  align-items: center;
  gap: 6px;
}
.customer .checkbox_box label {
  cursor: pointer;
}
.customer .button_box {
  margin-top: 40px;
}
.customer .button_box input {
  width: 100%;
  height: 50px;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  background-color: #0b5b96;
  border: none;
  outline: none;
  cursor: pointer;
}
